import React from 'react';
import { withRouter } from 'react-router-dom';

import { errorMessages, inputStatuses } from '../staticData';

class LogonPage extends React.Component {
  constructor(props) {
    super(props);

    const { clean } = inputStatuses;

    this.state = {
      inputValues: {
        name: '',
        password: '',
        repeatPassword: '',
      },

      nameInputStatus: clean,
      passInputStatus: clean,
      checkPassInputStatus: clean,
    };
  }

  keyPressHandler = ({ keyCode }) => {
    if (keyCode === 13) {
      this.clickHandler();
    }
  };

  componentDidMount() {
    window.addEventListener('keypress', this.keyPressHandler)
  }

  componentWillUnmount() {
    window.removeEventListener('keypress', this.keyPressHandler)
  }

  clickHandler = () => {
    const { inputValues: { name, password, repeatPassword }} = this.state;

    this.checkName(name);
    this.checkPassword(password);
    this.checkRepeatPassword(repeatPassword);
    console.log(this.isValid());
    if (this.isValid()) {
      this.logon();
    }
  };

  logon = () => {
    const {
      inputValues: { name, password },
    } = this.state;
    const { addUser, changeLogStatus, history } = this.props;

    const user = {
      name,
      password,
    };

    addUser(user);
    changeLogStatus(user);

    history.replace('/games');
  };

  setInputValue = (key, value) => {
    const { inputValues } = this.state;
    const { clean } = inputStatuses;

    switch (key) {
      case 'NAME':
        this.setState({
          inputValues: {
            name: value,
            password: inputValues.password,
            repeatPassword: inputValues.repeatPassword,
          },
        });

        return;
      case 'PASSWORD':
        this.setState({
          inputValues: {
            name: inputValues.name,
            password: value,
            repeatPassword: '',
          },
          checkPassInputStatus: clean,
        });

        return;
      case 'CHECKPASSWORD':
        this.setState({
          inputValues: {
            name: inputValues.name,
            password: inputValues.password,
            repeatPassword: value,
          },
        });

        return;
      default:
        return;
    }
  };

  isUserNameBusy(users, userName) {
    return users && users.some(({ name }) => userName === name);
  }

  checkName = name => {
    const { valid, error } = inputStatuses;
    const { wrongSymbols, nameIsBusy, invalidLengthName, emptyName } = errorMessages;
    const { users } = this.props;
    console.log(1);
    if (!name) {
      this.setState({
        nameInputStatus: {
          type: error,
          message: emptyName,
        },
      });

      return;
    }

    if (users && this.isUserNameBusy(users, name)) {
      this.setState({
        nameInputStatus: {
          type: error,
          message: nameIsBusy,
        },
      });

      return;
    }

    if (!/(^\w*$)/.test(name)) {
      this.setState({
        nameInputStatus: {
          type: error,
          message: wrongSymbols,
        },
      });

      return;
    }

    const { length } = name;

    if (length === 0 || length > 20) {
      this.setState({
        nameInputStatus: {
          type: error,
          message: invalidLengthName,
        },
      });

      return;
    }

    this.setState({
      nameInputStatus: valid,
    });
  };

  changeHandler = (input, e) => {
    const { value } = e.target;
    const { setInputValue } = this;

    setInputValue(input, value);
  };

  checkRepeatPassword = repeatPassword => {
    const { valid, error } = inputStatuses;
    const {
      inputValues: { password },
    } = this.state;
    const { passwordsDoesNotMatch } = errorMessages;

    if (!repeatPassword || repeatPassword !== password) {
      this.setState({
        checkPassInputStatus: {
          type: error,
          message: passwordsDoesNotMatch,
        },
      });

      return;
    }

    this.setState({
      checkPassInputStatus: valid,
    });
  };

  checkPassword = password => {
    const { valid, error } = inputStatuses;
    const { wrongSymbols, invalidLengthPass, wrongPassword } = errorMessages;

    if (!/(^\w*$)/.test(password) || !password) {
      this.setState({
        passInputStatus: {
          type: error,
          message: wrongSymbols,
        },
      });

      return;
    }

    const { length } = password;

    if (length < 4 || length > 10) {
      this.setState({
        passInputStatus: {
          type: error,
          message: invalidLengthPass,
        },
      });

      return;
    }

    this.setState({
      passInputStatus: valid,
    });
  };

  blurHandler = (input, e) => {
    const { value } = e.target;
    const { checkName, checkPassword, checkRepeatPassword } = this;

    switch (input) {
      case 'NAME':
        checkName(value);
        return;
      case 'PASSWORD':
        checkPassword(value);
        return;
      case 'CHECKPASSWORD':
        checkRepeatPassword(value);
        return;
      default:
        return;
    }
  };

  focusHandler = input => {
    const { editing } = inputStatuses;

    switch (input) {
      case 'NAME':
        this.setState({
          nameInputStatus: editing,
        });
        return;
      case 'PASSWORD':
        this.setState({
          passInputStatus: editing,
        });
        return;
      case 'CHECKPASSWORD':
        this.setState({
          checkPassInputStatus: editing,
        });
        return;
      default:
        return;
    }
  };

  isValid = () => {
    const {
      nameInputStatus,
      passInputStatus,
      checkPassInputStatus,
    } = this.state;
    const { valid, clean } = inputStatuses;

    console.log(      nameInputStatus,
        passInputStatus,
        checkPassInputStatus);

    return [nameInputStatus, passInputStatus, checkPassInputStatus].every(
      status => status === valid
    );
  };

  render() {
    const {
      inputValues,
      nameInputStatus,
      passInputStatus,
      checkPassInputStatus,
    } = this.state;
    const {
      type: nameInputStatusType,
      message: nameInputStatusMessage,
    } = nameInputStatus;
    const {
      type: passInputStatusType,
      message: passInputStatusMessage,
    } = passInputStatus;
    const {
      type: checkPassInputStatusType,
      message: checkPassInputStatusMessage,
    } = checkPassInputStatus;
    const { error, valid } = inputStatuses;

    return [
      <div className="logon">
        Sign Up
        <div
          className={`logon__input-title ${nameInputStatus === valid &&
            'logon__input-title--valid'}`}
        >
          Name
        </div>
        <div
          className={`message ${nameInputStatusType &&
            nameInputStatusType === error &&
            'message--error'}`}
        >
          {nameInputStatusMessage}
        </div>
        <input
          type="text"
          className={`logon__input ${(nameInputStatus === valid &&
            'logon__input--valid') ||
            (nameInputStatusType &&
              nameInputStatusType === error &&
              'logon__input--error') ||
            ''}`}
          onChange={this.changeHandler.bind(null, 'NAME')}
          onBlur={this.blurHandler.bind(null, 'NAME')}
          onFocus={this.focusHandler.bind(null, 'NAME')}
          value={inputValues.name}
        />
        <div
          className={`logon__input-title ${passInputStatus === valid &&
            'logon__input-title--valid'}`}
        >
          Password
        </div>
        <div
          className={`message ${passInputStatusType &&
            passInputStatusType === error &&
            'message--error'}`}
        >
          {passInputStatusMessage}
        </div>
        <input
          type="password"
          className={`logon__input ${(passInputStatus === valid &&
            'logon__input--valid') ||
            (passInputStatusType &&
              passInputStatusType === error &&
              'logon__input--error') ||
            ''}`}
          onChange={this.changeHandler.bind(null, 'PASSWORD')}
          onBlur={this.blurHandler.bind(null, 'PASSWORD')}
          onFocus={this.focusHandler.bind(null, 'PASSWORD')}
          value={inputValues.password}
        />
        <div
          className={`logon__input-title ${checkPassInputStatus === valid &&
            'logon__input-title--valid'}`}
        >
          Repeat Password
        </div>
        <div
          className={`message ${checkPassInputStatusType &&
            checkPassInputStatusType === error &&
            'message--error'}`}
        >
          {checkPassInputStatusMessage}
        </div>
        <input
          type="password"
          className={`logon__input ${(checkPassInputStatus === valid &&
            'logon__input--valid') ||
            (checkPassInputStatusType &&
              checkPassInputStatusType === error &&
              'logon__input--error') ||
            ''}`}
          onChange={this.changeHandler.bind(null, 'CHECKPASSWORD')}
          onBlur={this.blurHandler.bind(null, 'CHECKPASSWORD')}
          onFocus={this.focusHandler.bind(null, 'CHECKPASSWORD')}
          value={inputValues.repeatPassword}
        />
        <button
          className="submit-button"
          onClick={this.clickHandler}
        >
          SUBMIT
        </button>
      </div>
    ];
  }
}

export default withRouter(LogonPage);
