import React from 'react';
import { NavLink } from 'react-router-dom';

import LoginNotification from './loginNotification.jsx';

const GamesMenu = ({ isLoggedIn, alone }) => (
  <div className={`${alone ? "games__menu--alone" : "games__menu" }`}>
    {isLoggedIn && (
      <NavLink
        to="/games/invasion"
        className="game__link"
        activeClassName="game__link--active"
      >
        Elderly invasion
      </NavLink>
    )}
    <NavLink
      to="/games/coins"
      className="game__link"
      activeClassName="game__link--active"
    >
      Coins
    </NavLink>
    {!isLoggedIn && <LoginNotification message="to get more games..."/>}
  </div>
);

export default GamesMenu;
