import React from 'react';

import { NavLink } from 'react-router-dom';

const GamesHeader = ({ isLoggedIn }) => {
  return (
    <>
      <ul className={isLoggedIn ? "headerNav headerNav--wideMenu" : "headerNav"}>
        <li className="headerNav__element">
          <NavLink
            to="/games"
            className="headerNav__link"
            activeClassName="activeRed"
          >
            Games
          </NavLink>
        </li>
        {isLoggedIn && <li className="headerNav__element">
          <NavLink
            to="/scores"
            className="headerNav__link"
            activeClassName="activeBlue"
          >
            Scores
          </NavLink>
        </li>}
      </ul>
    </>
  );
};

export default GamesHeader;