import React from 'react';

export class Granny extends React.PureComponent {
 render() {
   return (<div className='granny' style={{left: `${this.props.info.position[0]}px`, top: `${this.props.info.position[1]}px`}}>
       <div className={this.props.info.isMoving ? "granny__body granny__running" : "granny__body"}>
         <div className='granny__head' />
         <div className='granny__leg_one' />
         <div className='granny__leg_two' />
       </div>
     </div>
   );
 }
}
