import React from 'react';

import { Route, Switch, Redirect, withRouter } from 'react-router-dom';

import Games from './games.jsx';
import LoginNotification from './loginNotification.jsx';
import Scores from './scores.jsx';
import PageNotFound from './pageNotFound.jsx';
import LoginPage from './loginPage.jsx';
import LogonPage from './logonPage.jsx';
import AppHeader from './appHeader.jsx';

class GamesApp extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      isLoggedIn: false,
      name: null,
      users: null,
    };
  }

  changeLogStatus = user => {
    if (user) {
      this.setState({
        isLoggedIn: true,
        name: user.name,
      });

      localStorage.setItem('loggedUser', JSON.stringify(user));
    } else {
      this.setState({
        isLoggedIn: false,
        name: null,
      });

      localStorage.setItem('loggedUser', '');
    }
  };

  addUser = user => {
    const users = [...this.state.users, user];

    this.setState({
      users,
    });

    localStorage.setItem('users', JSON.stringify(users));
  };


  logout = () => {
    this.changeLogStatus();
    this.props.history.push('/sign_in');
  };

  componentDidMount() {
    this.setState({
      users: JSON.parse(localStorage.getItem('users')) || [],
    });

    const loggedUser = localStorage.getItem('loggedUser');

    if (!loggedUser) {
      this.setState({
        isLoggedIn: false,
      });

      return;
    }

    this.setState({
      isLoggedIn: true,
      name: JSON.parse(loggedUser).name,
    });
  }

  render() {
    const { isLoggedIn, name } = this.state;

    return (
      <>
        <AppHeader isLoggedIn={isLoggedIn} name={name} logout={this.logout}/>
        <Switch>
          <Redirect exact from="/" to="/games"/>
          <Route path="/games" render={() => (<Games isLoggedIn={isLoggedIn} name={name} />)} />
          <Route path="/scores" render={() => {
            if (isLoggedIn) {
              return <Scores />
            } else {
              return <LoginNotification message="to see this page..."/>
            }
          }} />
          <Route path="/sign_in" render={() => (isLoggedIn ? <Redirect to='./games'/> : <LoginPage users={this.state.users} changeLogStatus={this.changeLogStatus}/>)} />
          <Route path="/sign_up" render={() => (isLoggedIn ? <Redirect to='./games'/> : <LogonPage users={this.state.users} addUser={this.addUser} changeLogStatus={this.changeLogStatus}/>)} />
          <Route component={PageNotFound} />
        </Switch>
      </>
    );
  }
}

export default withRouter(GamesApp);
