import React from 'react';

import ScoreField from './Scorefield.jsx';
import GameField from './Gamefield.jsx';
import GamesMenu from '../gamesMenu';

const arrowKeyCodes = [37, 38, 39, 40];
let x = 0;

const getGranny = () => ({
  position: [Math.trunc(Math.random() * 1050), Math.trunc(Math.random() * 550)],
  name: `granny-${(x = x + 1)}`,
});

const getGrannies = amount =>
  new Array(amount).fill(' ').map(() => getGranny());

export default class InvasionGameApp extends React.Component {
  state = {
    heroPosition: [0, 0],
    grannies: [],
    step: 1,
    speed: 10,
    heroIsMoving: false,
    heroMovingTimer: null,
    grannyMovingTimer: null,
    gameTimer: null,
    gameTime: 0,
    direction: null,
    score: 0,
    grannyAppearanceTime: 1000,
    gameIsOver: false,
    gameIsInProgress: false,
    scores: null,
  };

  moveHero = () => {
    const { direction, heroPosition, step } = this.state;
    let [x, y] = heroPosition;

    switch (direction) {
      case 'TOP':
        y = y - step;
        if (y < -90) {
          return;
        }
        break;
      case 'BOTTOM':
        y = y + step;
        if (y > 535) {
          return;
        }
        break;
      case 'LEFT':
        x = x - step;
        if (x < -95) {
          return;
        }
        break;
      case 'RIGHT':
        x = x + step;
        if (x > 1050) {
          return;
        }
        break;
      default:
        break;
    }

    const heroCenterX = x + 95;
    const heroCenterY = y + 95;

    const gotGranny = this.state.grannies.find(({ position }) => {
      const [Gx, Gy] = position;

      return (
        Gx < heroCenterX + 45 &&
        Gx > heroCenterX - 85 &&
        Gy < heroCenterY + 55 &&
        Gy > heroCenterY - 85
      );
    });

    let grannies = this.state.grannies;
    let score = this.state.score;

    if (gotGranny) {
      grannies = grannies.filter(g => g !== gotGranny);
      score = score + 1;
    }

    this.setState({
      heroPosition: [x, y],
      grannies,
      score,
    });
  };

  handleKeydown = e => {
    const { keyCode } = e;

    if (!arrowKeyCodes.includes(keyCode)) {
      return;
    }

    e.preventDefault();

    if (this.state.heroIsMoving) {
      return;
    }
    let newDirection;

    switch (keyCode) {
      case 37:
        newDirection = 'LEFT';
        break;
      case 38:
        newDirection = 'TOP';
        break;
      case 39:
        newDirection = 'RIGHT';
        break;
      case 40:
        newDirection = 'BOTTOM';
        break;
      default:
        break;
    }

    const timer = setInterval(() => {
      this.moveHero();
    }, this.state.speed);

    this.setState({
      heroIsMoving: true,
      direction: newDirection,
      heroMovingTimer: timer,
    });

    this.moveHero();
  };

  handleKeyup = e => {
    const { keyCode } = e;

    if (!arrowKeyCodes.includes(keyCode) || !this.state.heroIsMoving) {
      return;
    }

    const { heroMovingTimer } = this.state;
    clearInterval(heroMovingTimer);

    this.setState({
      heroIsMoving: false,
      heroMovingTimer: null,
      direction: null,
    });
  };

  endGame = () => {
    clearInterval(this.state.grannyMovingTimer);
    clearInterval(this.state.heroMovingTimer);
    clearInterval(this.state.gameTimer);

    this.setState(({ score, gameTime, scores }) => {
      console.log(score, gameTime);
      const scoreObj = {
        name: this.props.name,
        speed: (score / gameTime || 0).toFixed(3),
        date: new Date().toLocaleString('en-US', { year: 'numeric', month: 'long', day: 'numeric' }),
        score,
      };

      return {
        gameIsOver: true,
        gameInProgress: false,
        heroIsMoving: false,
        heroMovingTimer: null,
        direction: null,
        scores: [...scores, scoreObj],
      };
    });

    window.removeEventListener('keydown', this.handleKeydown);
    window.removeEventListener('keyup', this.handleKeyup);
  };

  setGrannyAppearance = time => (setInterval(() => {
    const granny = getGranny();

    this.setState({
      grannies: [...this.state.grannies, granny],
    });

    if (this.state.grannies.length >= 40) {
      this.endGame();
    }
  }, time));


  startGame = () => {
    this.setState({
      grannies: getGrannies(1),
      gameTime: 0,
      score: 0,
      grannyAppearanceTime: 1000,
      speed: 10,
      grannyMovingTimer: this.setGrannyAppearance(1000),
    });

    const gameTimer = setInterval(() => {
      const { gameTime, speed, grannyAppearanceTime, grannyMovingTimer } = this.state;

      if (gameTime && gameTime % 15 === 0 && gameTime < 120 && speed > 2) {
        this.setState({
          speed: speed - 1.5,
        })
      }

      if (gameTime && gameTime % 15 === 0) {
        grannyMovingTimer && clearInterval(grannyMovingTimer);

        const newAppearanceTime = Math.trunc(grannyAppearanceTime * 0.7);

        this.setState({
          grannyAppearanceTime: newAppearanceTime,
          grannyMovingTimer: this.setGrannyAppearance(newAppearanceTime),
        })
      }

      this.setState({
        gameTime: this.state.gameTime + 1,
      });
    }, 1000);

    this.setState({
      gameTimer,
      gameIsOver: false,
      gameIsInProgress: true,
    });

    window.addEventListener('keydown', this.handleKeydown);
    window.addEventListener('keyup', this.handleKeyup);
  };

  componentDidMount() {
    this.setState({
      scores: JSON.parse(localStorage.getItem('scores')) || [],
    });
  }

  componentWillUnmount() {
    localStorage.setItem('scores', JSON.stringify(this.state.scores));

    window.removeEventListener('keydown', this.handleKeydown);
    window.removeEventListener('keyup', this.handleKeyup);
  }

  render() {
    return (
      <>
        <GamesMenu isLoggedIn={this.props.isLoggedIn} />
        <div className="game">
          <div className="gameArea">
            {this.state.gameIsOver ? (
              <GameOver startGame={this.startGame} />
            ) : this.state.gameIsInProgress ? (
              <GameField
                direction={this.state.direction}
                heroPosition={this.state.heroPosition}
                heroIsMoving={this.state.heroIsMoving}
                grannies={this.state.grannies}
                gameIsInProgress={this.state.gameIsInProgress}
                gameTime={this.state.gameTime}
              />
            ) : (
              <GameMenu startGame={this.startGame} />
            )}
            <ScoreField
              score={this.state.score}
              grannies={this.state.grannies.length}
              gameTime={this.state.gameTime}
            />
          </div>
        </div>
      </>
    );
  }
}

const GameMenu = ({ startGame }) => (
  <div className="game__menu">
    Save all cute grannies from the outer space.
    <button className="game__start-button" onClick={startGame}>
      START GAME
    </button>
  </div>
);

const GameOver = ({ startGame }) => (
  <div className="gameOver">
    Game Over!
    <button className="game__start-button" onClick={startGame}>
      START AGAIN
    </button>
  </div>
);
