import React from 'react';
import { withRouter } from 'react-router-dom';

import { errorMessages, inputStatuses } from '../staticData';

class LoginPage extends React.Component {
  constructor(props) {
    super(props);

    const { clean } = inputStatuses;

    this.state = {
      nameInputValue: '',
      nameInputStatus: clean,
      passwordInputValue: '',
      passwordInputStatus: clean,
    };
  }

  keyPressHandler = ({ keyCode }) => {
    if (keyCode === 13) {
      this.clickHandler();
    }
  };

  componentDidMount() {
    window.addEventListener('keypress', this.keyPressHandler)
  }

  componentWillUnmount() {
    window.removeEventListener('keypress', this.keyPressHandler)
  }

  clickHandler = () => {
    const { nameInputValue, passwordInputValue } = this.state;

    this.checkName(nameInputValue);
    this.checkPasswordField(passwordInputValue);

    if (this.isValid()) {
      this.login();
    }
  };

  login = () => {
    const { users, history, changeLogStatus } = this.props;
    const { nameInputValue, passwordInputValue } = this.state;
    const { wrongPassword } = errorMessages;
    const { error } = inputStatuses;

    const user = users.find(({ name }) => name === nameInputValue);

    if (user.password !== passwordInputValue) {
      this.setState({
        passwordInputStatus: {
          type: error,
          message: wrongPassword,
        },
      });

      return;
    }

    changeLogStatus(user);
    history.replace('/games');
  };

  checkName = userName => {
    const { nameDoesNotExist, emptyName } = errorMessages;
    const { error, valid } = inputStatuses;

    if (!userName) {
      this.setState({
        nameInputStatus: {
          type: error,
          message: emptyName,
        },
      });

      return;
    }

    const user = this.props.users.find(({ name }) => userName === name);

    if (!user) {
      this.setState({
        nameInputStatus: {
          type: error,
          message: nameDoesNotExist,
        },
      });

      return;
    }

    this.setState({
      nameInputStatus: valid,
    });
  };

  changeInputHandler = (inputKey, { target: { value } }) => {
    const inputValueKey =
      (inputKey === 'NAME' && 'nameInputValue') ||
      (inputKey === 'PASSWORD' && 'passwordInputValue');

    this.setState({
      [inputValueKey]: value
    });
  };

  focusHandler = inputKey => {
    const inputStatusKey =
      (inputKey === 'NAME' && 'nameInputStatus') ||
      (inputKey === 'PASSWORD' && 'passwordInputStatus');

    const { editing } = inputStatuses;

    this.setState({
      [inputStatusKey]: editing,
    });
  };

  checkPasswordField = userPassword => {
    const { valid, error } = inputStatuses;
    const { emptyPass } = errorMessages;

    if (!userPassword) {
      this.setState({
        passwordInputStatus: {
          type: error,
          message: emptyPass,
        },
      });

      return;
    }

    this.setState({
      passwordInputStatus: valid,
    });
  };

  blurHandler = inputKey => {
    const { nameInputValue, passwordInputValue } = this.state;

    if (inputKey === 'NAME') {
      this.checkName(nameInputValue);
    } else if (inputKey === 'PASSWORD') {
      this.checkPasswordField(passwordInputValue);
    }
  };

  isValid = () => {
    const { valid, error, clean } = inputStatuses;
    const { nameInputStatus, passwordInputStatus, passwordInputValue } = this.state;

    return nameInputStatus === valid &&
        passwordInputStatus !== error &&
        passwordInputStatus !== clean &&
        passwordInputValue;
  };


  render() {
    const { error } = inputStatuses;
    const { nameInputStatus, passwordInputStatus } = this.state;
    const { type: nameInputStatusType, message: nameInputStatusMessage } = nameInputStatus;
    const { type: passInputStatusType, message: passInputStatusMessage } = passwordInputStatus;

    return (
      <div className="login">
        Sign in
        <div className="logon__input-title">Name</div>
        <div className={`message ${nameInputStatusType === error && 'message--error'}`}>
          {nameInputStatusMessage}
        </div>
        <input
          type="text"
          className={`login__input ${nameInputStatusType === error && 'login__input--error'}`}
          value={this.state.nameInputValue}
          onChange={this.changeInputHandler.bind(null, 'NAME')}
          onFocus={this.focusHandler.bind(null, 'NAME')}
          onBlur={this.blurHandler.bind(null, 'NAME')}
        />
        <div className="logon__input-title">Password</div>
        <div className={`message ${passInputStatusType === error && 'message--error'}`}>
          {passInputStatusMessage}
        </div>
        <input
          type="password"
          className={`login__input ${passInputStatusType === error && 'login__input--error'}`}
          value={this.state.passwordInputValue}
          onChange={this.changeInputHandler.bind(null, 'PASSWORD')}
          onFocus={this.focusHandler.bind(null, 'PASSWORD')}
          onBlur={this.blurHandler.bind(null, 'PASSWORD')}
        />
        <button className="submit-button" onClick={this.clickHandler}>SUBMIT</button>
      </div>
    );
  }
}

export default withRouter(LoginPage);
