import React from 'react';
import { Route, Switch, NavLink } from 'react-router-dom';

import GamesHeader from './gamesHeader.jsx';
import PageNotFound from './pageNotFound.jsx';

class Scores extends React.Component {
  state = {
    scores: [],
    users: [],
  };

  componentDidMount() {
    this.setState({
      scores: JSON.parse(localStorage.getItem('scores')) || [],
      users: JSON.parse(localStorage.getItem('users')) || [],
    });
  }

  render() {
    return (
      <>
        <GamesHeader isLoggedIn={true} />
        <Switch>
          <Route
            exact
            path="/scores"
            render={() => <ScoresTable scores={this.state.scores} />}
          />
          ;
          <Route
            exact
            path="/scores/:username"
            render={routeProps => <UserScorePage {...routeProps} scores={this.state.scores} users={this.state.users} />}
          />
          ;
          <Route component={PageNotFound} />
        </Switch>
      </>
    );
  }
}

const UserScorePage = props => {
  console.dir(props);
  const userName = props.match.params.username;
  const { scores, users } = props;

  const user = users.find(({ name }) => userName === name);

  if (!user) {
    return (<PageNotFound />);
  }

  const games = scores.filter(({ name }) => userName === name);

  let maxScore = 0;
  let maxScoreGameDate = null;

  games.forEach(({ score, date }) => {
    if (score > maxScore) {
      maxScore = score;
      maxScoreGameDate = date;
    }
  });

  return (
    <>
      <div className="userScorePageTitle">User score page.</div>
      <div className="userScorePage">
        <div className="userScorePage__img" />
        <div className="userScorePage__infoBlock" >
          <div className="userScorePage__infoBlock__info">Name: {userName}</div>
          <div className="userScorePage__infoBlock__info">Games played: {games.length}</div>
          {Boolean(games.length) && <div className="userScorePage__infoBlock__info">Best game scores: {maxScore}</div>}
          {Boolean(games.length) && <div className="userScorePage__infoBlock__info">Date of best game: {maxScoreGameDate}</div>}
        </div>
      </div>
    </>
  );
};

const ScoresTable = ({ scores }) => (
  <table className="scoresTable">
    <thead>
      <ScoresTableHeader />
    </thead>
    <tbody>
      {scores.length ? (
        scores
          .slice(0, 15)
          .sort((scoreObjOne, scoreObjTwo) => {
            const scoresOne = scoreObjOne.score;
            const scoresTwo = scoreObjTwo.score;

            return scoresTwo - scoresOne;
          })
          .map((scoresObj, i) => {
            const { name, score, speed, date } = scoresObj;

            return (
              <ScoresRow
                name={name}
                score={score}
                speed={speed}
                date={date}
                key={`${name}${i}`}
                position={i + 1}
              />
            );
          })
      ) : (
        <NoScores />
      )}
    </tbody>
  </table>
);

const ScoresTableHeader = () => (
  <tr className="scoresTable__header">
    <th className="scoresTable__header__cell">№</th>
    <th className="scoresTable__header__cell">Name</th>
    <th className="scoresTable__header__cell">
      Speed
      <span className="unit">{' (grannies/sec)'}</span>
    </th>
    <th className="scoresTable__header__cell">
      Scores
      <span className="unit">{' (grannies)'}</span>
    </th>
    <th className="scoresTable__header__cell">Date</th>
  </tr>
);

const ScoresRow = ({ name, score, speed, position, date }) => (
  <tr className="scoresTable__row">
    <td className="scoresTable__row__cell">{position}</td>
    <td className="scoresTable__row__cell">
      <NavLink to={`/scores/${name}`} className="scoresTable__link">{name}</NavLink>
    </td>
    <td className="scoresTable__row__cell">{speed}</td>
    <td className="scoresTable__row__cell">{score}</td>
    <td className="scoresTable__row__cell">{date}</td>
  </tr>
);

const NoScores = () => (
  <tr className="scoresTable__header">
    <td className="scoresTable__header__no-scores" colSpan="5">
      No saved scores :(
    </td>
  </tr>
);

export default Scores;
