import React from 'react';

let legDirection = '--right';

const getStyle = (time, position) => {
  let boxShadow;

  if (time < 20) {
    boxShadow = `0 0 0 150rem rgba(0, 0, 0, 0), inset 0 0 1rem rgba(0, 0, 0, 0), inset 0 0 0.5rem rgba(0, 0, 0, 0)`;
  } else if (time < 30) {
    boxShadow = `0 0 0 150rem rgba(0, 0, 0, 0.1), inset 0 0 1rem rgba(0, 0, 0, 0.1), inset 0 0 0.5rem rgba(0, 0, 0, 0.1)`;
  } else {
    boxShadow = `0 0 0 150rem rgba(0, 0, 0, 1), inset 0 0 1rem rgba(0, 0, 0, 0.95), inset 0 0 0.5rem rgba(0, 0, 0, 0.95)`;
  }

  return {
    boxShadow,
    left: `${position[0]}px`,
    top: `${position[1]}px`,
  };
};

export const Hero = ({ direction, position, gameTime, heroIsMoving }) => {
  if (direction === 'LEFT') {
    legDirection = '--left';
  } else if (direction === 'RIGHT') {
    legDirection = '--right';
  }

  return (
    <div className="hero" style={getStyle(gameTime, position)}>
      <div className={heroIsMoving ? 'body running' : 'body'}>
        <div className={`head head${legDirection}`} />
        <div className={`leg_one leg_one${legDirection}`} />
        <div className={`leg_two leg_two${legDirection}`} />
      </div>
    </div>
  );
};
