import React from 'react';

import { Route, Switch } from 'react-router-dom';

import InvasionGameApp from './elderly/GameApp.jsx';
import CoinsApp from './coins/CoinsApp.jsx';
import PageNotFound from './pageNotFound.jsx';
import GamesMenu from './gamesMenu.jsx';
import GamesHeader from './gamesHeader.jsx';
import LoginNotification from './loginNotification.jsx';

const Games = ({ isLoggedIn, name }) => (
  <>
  <GamesHeader isLoggedIn={isLoggedIn}/>
  <div  className="games">
    <Switch>
      <Route exact path="/games" render={() => (<GamesMenu isLoggedIn={isLoggedIn} alone/>)} />
      <Route path="/games/invasion" render={() => isLoggedIn ? <InvasionGameApp name={name} isLoggedIn={isLoggedIn}/> : <LoginNotification message="to see this page..."/>} />
      <Route path="/games/coins" render={() => (<CoinsApp isLoggedIn={isLoggedIn} name={name}/>)} />
      <Route component={PageNotFound} />
    </Switch>
  </div>
  </>
);

export default Games
