import React from 'react';
import uniqid from 'uniqid';

import GamesMenu from '../gamesMenu.jsx';
import { levels } from './StaticData/levels';

export default class CoinsApp extends React.Component {
  state = {
    gameIsOver: false,
    levelIsOver: true,
    score: 0,
    coins: [],
    idOrder: [],
    level: 1,
    gameTimer: null,
    gameTime: 0,
    scores: null,
  };

  componentDidMount() {
    const scores = JSON.parse(localStorage.getItem('coinsScores')) || [];

    this.setState({
      scores,
    });
  }

  resetGame = () => {
    this.setState(
      {
        level: 1,
        gameIsOver: false,
        levelIsOver: false,
        score: 0,
        levelDelay: null,
      },
      this.gameInit
    );
  };

  levelOver = () => {
    this.setState({
      coins: this.state.coins.map(coin => ({ ...coin, opened: true })),
      gameIsOver: false,
      levelIsOver: true,
      level: this.state.level + 1,
      score: this.state.score + 500,
    });
  };

  generatePosition() {
    const xMin = 55;
    const xMax = 690;
    const yMin = 55;
    const yMax = 590;

    return [
      Math.round(xMin + (xMax - xMin) * Math.random()),
      Math.round(yMin + (yMax - yMin) * Math.random()),
    ];
  }

  generatePositions = amount => {
    const positions = [];

    for (let i = 0; i < amount; i++) {
      while (true) {
        const newPosition = this.generatePosition();
        const positionIsValid = positions.every(position => {
          return (
            Math.abs(position[0] - newPosition[0]) > 90 ||
            Math.abs(position[1] - newPosition[1]) > 90
          );
        });

        if (positionIsValid) {
          positions.push(newPosition);
          break;
        }
      }
    }

    return positions;
  };

  stepToNextCoin = coinId => {
    console.dir(this);
    const { idOrder, coins, score, level } = this.state;

    if (idOrder.length === 1) {
      this.levelOver();

      return;
    }

    const scoreMultiply = coins.length - idOrder.length + 1;

    this.setState({
      idOrder: idOrder.slice(1),
      coins: coins.map(coin => {
        const { id } = coin;

        return id === coinId ? { ...coin, opened: true, error: false } : coin;
      }),
      score: score + 5 * scoreMultiply * level,
    });
  };

  handleUserMistake = () => {
    const { idOrder, coins } = this.state;

    const newScore = {
      score: this.state.score,
      name: this.props.name || 'Shy girl',
    };

    const newScores = [...this.state.scores, newScore].sort((a, b) => {
      const { score: scoreA } = a;
      const { score: scoreB } = b;

      return scoreB - scoreA;
    });

    localStorage.setItem('coinsScores', JSON.stringify(newScores));

    this.setState({
      idOrder: idOrder.slice(1),
      coins: coins.map(coin => {
        const { opened } = coin;
        return opened ? coin : { ...coin, opened: true, error: true };
      }),
      gameIsOver: true,
      levelDelay: null,
      scores: newScores,
    });
  };

  checkCoin = coinId => {
    const { state: { idOrder }, stepToNextCoin, handleUserMistake } = this;
    const correctID = idOrder[0];

    if (coinId === correctID) {
      stepToNextCoin(coinId);
    } else {
      handleUserMistake()
    }
  };

  generateCoins = count => {
    const coins = [];
    const positions = this.generatePositions(count);

    for (let i = 0; i < count; i++) {
      let id = uniqid('coin');

      coins.push({
        id,
        onClick: this.checkCoin.bind(null, id),
        opened: true,
        error: false,
        position: positions[i],
      });
    }

    return coins;
  };

  levelInit = (coinsAmount, delay) => {
    const coins = this.generateCoins(coinsAmount);
    this.setState({
      levelIsOver: false,
      coins,
      idOrder: coins.map(({ id }) => id),
      levelDelay: delay / 1000,
      gameTime: delay / 1000,
      gameTimer: setInterval(() => {
        this.setState({
          gameTime: (this.state.gameTime - 0.01).toFixed(2),
        });
      }, 10),
    });

    setTimeout(() => {
      let newcoins = coins.map(coin => ({ ...coin, opened: false }));
      clearInterval(this.state.gameTimer);

      this.setState({
        coins: newcoins,
        gameTime: 0,
      });
    }, delay);
  };

  gameInit = () => {
    const { level } = this.state;
    const levelToFind = level >= levels.length ? 'last' : level;
    const { coins, delay } = levels.find(({ level }) => level === levelToFind);

    this.levelInit(coins, delay);
  };

  render() {
    return (
      <>
        <GamesMenu isLoggedIn={this.props.isLoggedIn} />
        <div className="coinsGame">
          <GameField
            coins={this.state.coins}
            gameIsOver={this.state.gameIsOver}
            levelIsOver={this.state.levelIsOver}
            level={this.state.level}
            gameInit={this.gameInit}
            resetGame={this.resetGame}
            gameTime={this.state.gameTime}
            levelDelay={this.state.levelDelay}
          />
          <ScoreField score={this.state.score} scores={this.state.scores} />
        </div>
      </>
    );
  }
}

const GameField = ({
  levelIsOver,
  gameIsOver,
  coins,
  gameInit,
  level,
  resetGame,
  gameTime,
  levelDelay,
}) => {
  return (
    <div className="coinsGame__gameField">
      {levelIsOver && level === 1 && <HelloPage gameInit={gameInit} />}
      {gameIsOver && <GameOverScreen resetGame={resetGame} />}
      {levelIsOver && level !== 1 && (
        <StartScreen gameInit={gameInit} level={level} />
      )}
      {!gameIsOver && !levelIsOver && (
        <Timer gameTime={gameTime} levelDelay={levelDelay} />
      )}
      {coins.map((coin, i) => {
        const { id, onClick, opened, error, position } = coin;
        return (
          <div
            className={`coinsGame__coin 
                ${error && 'coinsGame__coin--error'} 
                ${
                  opened ? 'coinsGame__coin--opened' : 'coinsGame__coin--closed'
                } 
                ${levelIsOver && 'coinsGame__coin--won'}
                `}
            onClick={opened ? null : onClick}
            key={id}
            style={{
              left: `${position[0]}px`,
              top: `${position[1]}px`,
            }}
          >
            {opened && i + 1}
          </div>
        );
      })}
    </div>
  );
};

const HelloPage = ({ gameInit }) => (
  <div className="coinsGame__helloPage">
    Turn over all the coins starting from the first in terms of nominal value
    growth. With each level, the number of coins will increase, and the time for
    viewing will decrease.
    <div className="coinsGame__helloPage__GL">Good luck!</div>
    <button className="coinsGame__startLevel" onClick={gameInit}>
      START
    </button>
  </div>
);

const StartScreen = ({ level, gameInit }) => (
  <div className="coinsGame__levelNotification">
    Well done! Next level: {level}
    <button className="coinsGame__startLevel" onClick={gameInit}>
      START
    </button>
  </div>
);

const GameOverScreen = ({ resetGame }) => (
  <div className="coinsGame__gameOverNotification">
    Game over
    <button className="coinsGame__restartGame" onClick={resetGame}>
      TRY AGAIN
    </button>
  </div>
);

const Timer = ({ gameTime, levelDelay }) => (
  <div
    className="coinsGame__Timer"
    style={{
      width: gameTime === levelDelay && '100%',
      transitionDuration: `${Math.trunc(levelDelay)}s`,
    }}
  />
);

const ScoreField = ({ score, scores }) => {
  return (
    <div className="coinsGame__scoreField">
      <div className="coinsGame__scoreField__currentScores">Score: {score}</div>
      <table  className="coinsGame__scoreField__table">
        <thead>
          <tr>
            <th className="coinsGame__scoreField__header">№</th>
            <th className="coinsGame__scoreField__header">Name</th>
            <th className="coinsGame__scoreField__header">Scores</th>
          </tr>
        </thead>
        <tbody>
          {scores && scores.slice(0, 15).map((scoreObj, i) => {
            const { score, name } = scoreObj;
            return (
              <tr key={`${name}${i}`}>
                <td>{i+1}</td>
                <td>{name}</td>
                <td>{score}</td>
              </tr>
            );
          })}
        </tbody>
      </table>
    </div>
  );
};
