import React from 'react';

export default class ScoreField extends React.Component{
  render() {
    return (
      <div className='scoreField'>
        <div className={this.props.grannies < 35 ? "grannies-counter" : "grannies-counter grannies-counter--danger"}>Grannies: {this.props.grannies} / 40</div>
        <div>
          Time: {this.props.gameTime}
          <span className="unit">
              {' sec'}
            </span>
        </div>
        <div>
          Score: {this.props.score}
          <span className="unit">
              {' grannies'}
            </span>
        </div>
        <div>
          Speed: {(this.props.score / this.props.gameTime || 0).toFixed(2)}
          <span className="unit">
              {' grannies/sec'}
            </span>
        </div>
      </div>
    );
  }
}